@import url(https://fonts.googleapis.com/css?family=Quicksand:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{font-family:'Quicksand', sans-serif}.warning{background-color:#af1d1d;color:#fff;padding:15px}input[type=text],input[type=password]{box-sizing:border-box}.text-title{font-size:16px;font-weight:bold}.text-paragraph{font-size:13px}header{background-color:#1995AD;color:white;text-align:center;font-weight:bold;font-size:20px;padding:30px}footer{background-color:#BCBABE;padding:15px 30px;margin:auto;margin-top:5vh;max-width:1300px}.notification-panel,.server-panel,.server-options-panel{max-width:1300px;margin:auto;padding:0vh 3vw}.notification-panel div,.server-panel div,.server-options-panel div{margin:10px 0px}.server-options-panel{margin-top:5vh}.notification-panel-wrapper{background-color:#A1D6E2;padding:20px;text-align:center}.server-panel{text-align:center}.input-area .input-row label{display:block;margin-bottom:5px}.input-area .input-row input{display:inline-block;width:100%}.server-url input{width:50%}.video-wrapper{background-color:#F1F1F2}button{border:none;color:#fff;font-family:'Quicksand', sans-serif}button:hover{cursor:pointer}input{padding:5px}.solid{box-shadow:0 3px 4px #aaa;background-color:#000;padding:10px 30px}.solid:hover{box-shadow:0 3px 7px #555;background-color:#1995AD}

